import React from 'react';
import {Route, Switch} from 'react-router-dom';

import {PageLayout} from '../../../Styled/PageLayout';
import {BlindfoldIntroPage} from '../BlindfoldIntroPage/BlindfoldIntroPage';
import {BlindfoldLevel1} from '../BlindfoldLevel1/BlindfoldLevel1';
import {BlindfoldSessionPage} from '../BlindfoldSessionPage/BlindfoldSessionPage';

export function BlindfoldRoutes() {
    return (
        <PageLayout>
            <Switch>
                <Route path="/blindfold/session">
                    <BlindfoldSessionPage />
                </Route>
                <Route path="/blindfold/intro">
                    <BlindfoldIntroPage />
                </Route>
                <Route path="/blindfold/1">
                    <BlindfoldLevel1 />
                </Route>
            </Switch>
        </PageLayout>
    );
}
