import {css} from '@emotion/css';
import {Box} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useMemo} from 'react';

import {ALL_TASKS} from '../../sessions/sessions';
import {PageLayout} from '../Styled/PageLayout';

export interface BrainProps {}

function random(max: number): number {
    return Math.trunc(Math.random() * max);
}

function randomNotSame(max: number, previous: number): number {
    return (previous + random(ALL_TASKS.length - 1) + 1) % ALL_TASKS.length;
}

export function Brain(props: BrainProps): ReactElement {
    const colors = useMemo(() => {
        const total = 30;

        let previousLabelIndex = -1;
        return Array(total)
            .fill(null)
            .map(() => {
                const labelIndex =
                    previousLabelIndex === -1
                        ? random(ALL_TASKS.length)
                        : randomNotSame(ALL_TASKS.length, previousLabelIndex);
                const colorIndex = randomNotSame(ALL_TASKS.length, labelIndex);

                previousLabelIndex = labelIndex;

                return {
                    label: ALL_TASKS[labelIndex].label,
                    color: ALL_TASKS[colorIndex].color,
                };
            });
    }, []);

    return (
        <PageLayout>
            <Box p={2} className={styles.color}>
                {colors.map((color, i) => (
                    <span key={i} style={{color: color.color}}>
                        {color.label}{' '}
                    </span>
                ))}
            </Box>
        </PageLayout>
    );
}

const styles = {
    color: css`
        font-size: 3em;
        text-transform: uppercase;
        font-weight: bold;
        text-shadow: 0 1px 1px black;
    `,
};
