import type {ReactElement} from 'react';
import React, { useMemo,} from 'react';

interface Circle {
  x: number;
  y: number;
  radius: number;
}

export function Cloud(): ReactElement {
  const count = 20;
  const width = 300;
  const height = 200;

  const circles = useMemo<Circle[]>(() => Array(count).fill(null).map((c, i) => ({
    x: width / 2 + (2 * Math.random() - 1) ** 3 * width,
    y: height - (1 - Math.sqrt(1 - Math.random() ** 2)) * height,
    radius: 30,
  })), []);

  return (
    <svg width={width} height={height}>
      {circles.map((c, i) => <circle key={i} cx={c.x} cy={c.y} r={c.radius} fill="rgb(0,0,0,0.5)"/>)}
    </svg>
  );
}

export function CloudPage(): ReactElement {
  return <div/>;
}
