import {Clear} from '@mui/icons-material';
import {Box, IconButton} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useCallback, useEffect, useMemo, useState} from 'react';
import {Link} from 'react-router-dom';

import {playAudio} from '../../utility/playAudio';
import type {Round, Task} from '../Pages/assignments';
import {BlindfoldRound} from './BlindfoldRound';
import {SessionEnd} from './SessionEnd';
import {SessionStart} from './SessionStart';
import type {SessionRecord} from './types';

const TOTAL_ROUNDS = 10;

export interface BlindfoldSessionProps {
    lessonId?: string;
    tasks: Task[];
}

export function BlindfoldSession({
    lessonId,
    tasks,
}: BlindfoldSessionProps): ReactElement {
    const [index, setIndex] = useState(0);
    const [started, setStarted] = useState<boolean>(false);
    const [roundStart, setRoundStart] = useState<Date>(new Date());
    const [sessionRecord, setSessionRecord] = useState<SessionRecord>({
        start: new Date(),
        end: new Date(),
        answers: [],
        summary: {},
        score: 0,
    });

    useEffect(() => {
        setIndex(0);
    }, [tasks]);

    useEffect(() => {
        console.log('Round start!', new Date());
        setRoundStart(new Date());
        if (index === TOTAL_ROUNDS - 5) {
            playAudio('sounds/5_rounds_remaining.mp3');
        }
        if (index === TOTAL_ROUNDS - 1) {
            playAudio('sounds/last_round.mp3');
        }
        if (index === TOTAL_ROUNDS) {
            playAudio('sounds/congratulations.mp3');
        }
    }, [index]);

    const rounds = useMemo(() => {
        const result: Round[] = [];
        for (let i = 0; i < TOTAL_ROUNDS; i++) {
            const randomPick = Math.trunc(Math.random() * tasks.length);
            result.push({
                order: i,
                answerIndex: randomPick,
                task: tasks[randomPick],
            });
        }
        return result;
    }, [tasks]);

    const onAnswer = (correct: boolean, givenAnswer: number, time: Date) => {
        const correctAnswer = rounds[index].answerIndex;
        const answers = [
            ...sessionRecord.answers,
            {
                correct,
                givenAnswer,
                start: roundStart,
                end: time,
                task: rounds[index].task,
            },
        ];

        const {summary} = sessionRecord;
        summary[correctAnswer] = summary?.[correctAnswer] ?? {
            correct: 0,
            wrong: 0,
            task: tasks[correctAnswer],
        };
        if (correct) {
            summary[correctAnswer].correct++;
        } else {
            summary[correctAnswer].wrong++;
        }

        const correctAnswers = answers.filter(a => a.correct).length;
        const score = Math.max(correctAnswers - answers.length / 2, 0);

        console.log('OnAnswer', answers);

        setSessionRecord({...sessionRecord, answers, score});
    };

    const onNext = useCallback(() => {
        setIndex(i => i + 1);
    }, []);

    if (index >= rounds.length) {
        return <SessionEnd lessonId={lessonId} session={sessionRecord} />;
    }

    return (
        <>
            <Box position="absolute" sx={{right: 0, top: 0, zIndex: 1}}>
                <IconButton component={Link} to="/blindfold/1">
                    <Clear />
                </IconButton>
            </Box>

            {started ? (
                <BlindfoldRound
                    tasks={tasks}
                    round={rounds[index]}
                    totalRounds={TOTAL_ROUNDS}
                    onAnswer={onAnswer}
                    onNext={onNext}
                />
            ) : (
                <SessionStart tasks={tasks} onNext={() => setStarted(true)} />
            )}
        </>
    );
}
