import {css, cx} from '@emotion/css';
import {Box, Stack} from '@mui/material';
import React, {useRef, useState} from 'react';
import {CSSTransition} from 'react-transition-group';

enum BlindfoldIntroSteps {
    Welcome,
    ScreenNavigation,
    BlindfoldOn,
}

/*
Welcome to the practice of seeing blindfolded! You'll need a blindfold to try this out.

You'll be trying to perceive colors shown on the screen of your device without seeing them.

In this intro, you'll try to distinguish between white and black.

The screen will be divided in two halves, if you think it is white, you'll tap on the left.

If you think the screen is black, you'll tap on the right.

Let's try it!
It is black.
It is white.
It is green.
It is red.
It is brown.
It is pink.
It is orange.
It is yellow.
It is blue.
It is purple.
*/

export function BlindfoldIntroPage() {
    // const tasks = useMemo(() => [ALL_TASKS[0], ALL_TASKS[1]], []);
    const [step, setStep] = useState<BlindfoldIntroSteps>(
        BlindfoldIntroSteps.Welcome,
    );
    const step1 = useRef(null);
    const step2 = useRef(null);

    return (
        <Box className={styles.container}>
            <CSSTransition
                in={step === BlindfoldIntroSteps.Welcome}
                classNames="fade"
                addEndListener={() => {}}
                unmountOnExit
                timeout={1000}
                nodeRef={step1}
            >
                <Box
                    ref={step1}
                    className={cx(styles.layout)}
                    onClick={() =>
                        setStep(BlindfoldIntroSteps.ScreenNavigation)
                    }
                >
                    <div>Welcome to the seeing blindfolded practice!</div>
                    <div>
                        You'll be trying to perceive colors shown on the screen
                        of your device.
                    </div>
                </Box>
            </CSSTransition>
            <CSSTransition
                in={step === BlindfoldIntroSteps.ScreenNavigation}
                classNames="fade"
                addEndListener={() => {}}
                unmountOnExit
                timeout={1000}
                nodeRef={step2}
            >
                <Box
                    ref={step2}
                    onClick={() =>
                        setStep(BlindfoldIntroSteps.ScreenNavigation)
                    }
                >
                    The screen will be divided in two halves.
                    <Stack direction="row" justifyContent="space-between">
                        <Box
                            style={{backgroundColor: 'white', color: 'black'}}
                            flex={1}
                        >
                            Tap here if you think the screen is WHITE
                        </Box>
                        <Box style={{backgroundColor: 'black'}} flex={1}>
                            Tap here if you think the screen is BLACK
                        </Box>
                    </Stack>
                </Box>
            </CSSTransition>
        </Box>
    );

    // return <BlindfoldSession tasks={tasks} />;
}

const styles = {
    container: css`
        background: #022e2a;
        background: radial-gradient(ellipse at center, #022e2a, #010101);
        color: white;
        height: 100vh;
        width: 100vw;
    `,
    layout: css`
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        gap: 2rem;
        text-align: center;
        align-items: center;
        justify-content: center;
        padding: 4rem;
    `,
};
