import React, {useMemo} from 'react';

import {ALL_TASKS} from '../../../../sessions/sessions';
import {useQueryParam} from '../../../../utility/useQueryParam';
import {BlindfoldSession} from '../../../BlindfoldSession/BlindfoldSession';
import type {Task} from '../../assignments';
import {LESSONS} from '../BlindfoldLevel1/lessons';

export function useTasks(lessonId?: string): Task[] {
    const taskIds = useQueryParam('tasks');

    return useMemo<Task[]>(() => {
        if (taskIds) {
            return (
                (taskIds
                    ?.split(',')
                    .map(t => ALL_TASKS.find(at => at.id === t))
                    .filter(t => !!t) as Task[]) ?? []
            );
        }
        if (lessonId) {
            return (
                (LESSONS[lessonId].tasks
                    .map(t => ALL_TASKS.find(at => at.id === t))
                    .filter(t => !!t) as Task[]) ?? []
            );
        }

        return [];
    }, [taskIds, lessonId]);
}

export function BlindfoldSessionPage() {
    const lessonId = useQueryParam('id');
    const tasks = useTasks(lessonId);

    return <BlindfoldSession lessonId={lessonId} tasks={tasks} />;
}
