import {Box, ButtonBase, Stack} from '@mui/material';
import type {ReactElement, ReactNode} from 'react';
import React, {useMemo} from 'react';
import {Link} from 'react-router-dom';

import {ALL_TASKS} from '../../../../sessions/sessions';
import type {SessionRecord} from '../../../BlindfoldSession/types';
import {LESSONS} from './lessons';

export interface SessionButtonProps {
    label: ReactNode;
    lessonId: string;
}

export function SessionButton({
    label,
    lessonId,
}: SessionButtonProps): ReactElement {
    const records = useMemo<SessionRecord[]>(() => {
        const json = localStorage.getItem(`lesson-${lessonId}.sessions`);
        return json ? JSON.parse(json) : [];
    }, [lessonId]);

    const score = records.reduce((acc, next) => acc + next.score, 0);

    const tasks = useMemo(
        () =>
            LESSONS[lessonId].tasks.map(t => ALL_TASKS.find(at => at.id === t)),
        [lessonId],
    );

    return (
        <Stack direction="row" alignItems="center">
            <Box
                sx={{
                    width: '4rem',
                    height: '4rem',
                    marginRight: '-3.6rem',
                    borderRadius: '100%',
                    backgroundColor: tasks[0]?.color,
                    boxShadow: '0 3px 6px black',
                }}
            />
            <ButtonBase
                component={Link}
                to={`/blindfold/session?id=${lessonId}`}
                sx={{
                    backgroundColor: theme => theme.palette.background.default,
                    borderRadius: '100%',
                    // border: '1px solid',
                    // borderColor: 'primary.main',
                    width: '5rem',
                    height: '5rem',
                    fontSize: '1.2rem',
                    transition: 'all .3s ease-out',
                    ':hover': {
                        transform: 'scale(1.1)',
                    },
                    boxShadow: '0 3px 6px #000',
                }}
            >
                {/* <CircularProgress variant="determinate" value={percentage} />*/}
                {score === 0 ? '-' : score}
            </ButtonBase>
            <Box
                sx={{
                    width: '4rem',
                    height: '4rem',
                    marginLeft: '-3.6rem',
                    borderRadius: '100%',
                    backgroundColor: tasks[1]?.color,
                    boxShadow: '0 3px 6px #000',
                }}
            />
        </Stack>
    );
}
