import {cx} from '@emotion/css';
import TouchAppRoundedIcon from '@mui/icons-material/TouchAppRounded';
import {Box, Typography} from '@mui/material';
import type {ReactElement} from 'react';
import React from 'react';

import {typography} from '../../styles/typography';
import type {Task} from '../Pages/assignments';
import {useEventListener} from '../Pages/useEventListener';
import {styles} from './styles';

interface StartProps {
    onNext: () => void;
    tasks: Task[];
}

export function SessionStart({onNext, tasks}: StartProps): ReactElement {
    useEventListener('keydown', onNext);

    return (
        <Box
            className={cx(styles.withDarkerBackground)}
            onClick={onNext}
            p={2}
            minHeight="100vh"
        >
            <Typography paragraph>
                Put on your blindfold and press any key or click anywhere to
                start the exercise.
            </Typography>
            <p>Screen:</p>
            {tasks.map(task => (
                <Box
                    key={task.id}
                    display="flex"
                    alignItems="stretch"
                    justifyContent="center"
                    width="300px"
                    height="180px"
                    borderRadius="5px"
                    mb={1}
                    marginLeft="auto"
                    marginRight="auto"
                    sx={{backgroundColor: task.color}}
                >
                    {tasks.map((t, i) => (
                        <Box
                            key={t.id}
                            flexDirection="column"
                            flex={1}
                            display="flex"
                            alignItems="center"
                            justifyContent="center"
                        >
                            {task.id === t.id && (
                                <>
                                    <span
                                        className={typography.contrastingText}
                                    >
                                        {i + 1} {t.label}
                                    </span>
                                    <TouchAppRoundedIcon fontSize="small" />
                                </>
                            )}
                        </Box>
                    ))}
                </Box>
            ))}
        </Box>
    );
}
