import {cx} from '@emotion/css';
import {Box, ButtonBase, debounce, Stack} from '@mui/material';
import CircularProgress from '@mui/material/CircularProgress';
import type {ReactElement} from 'react';
import React, {useCallback, useEffect, useRef, useState} from 'react';
import {CSSTransition} from 'react-transition-group';

import {playAudio} from '../../utility/playAudio';
import type {Round, Task} from '../Pages/assignments';
import {useEventListener} from '../Pages/useEventListener';
import {useWebSpeechApi} from '../Pages/useWebSpeechApi';
import {styles} from './styles';

interface BlindfoldedRoundProps {
    tasks: Task[];
    round: Round;
    totalRounds: number;
    onAnswer: (correct: boolean, givenAnswer: number, time: Date) => void;
    onNext: () => void;
}

export function BlindfoldRound({
    tasks,
    totalRounds,
    round,
    onAnswer,
    onNext,
}: BlindfoldedRoundProps): ReactElement {
    const [answering, setAnsweringImpl] = useState<boolean>(false);
    const answeringRef = useRef<boolean>(false);
    const [answered, setAnswered] = useState<boolean>(false);
    const loadingIndicatorRef = useRef(null);

    useEffect(() => {
        setAnswered(false);

        playAudio('sounds/notification.mp3');
    }, [round]);

    const setAnswering = (value: boolean) => {
        answeringRef.current = value;
        setAnsweringImpl(value);
    };

    const submitAnswer = useCallback(
        async (answer: number) => {
            if (answer >= tasks.length) {
                return;
            }
            setAnswering(true);
            const answerTime = new Date();
            console.log('Answer time:', answerTime);

            const correct = answer === round.answerIndex;

            try {
                await playAudio(tasks[answer].audioFile);

                await playAudio(round.task.rightAnswerAudioFile);
            } catch (e) {
                console.error('Error playing audio:', e);
            }

            onAnswer(correct, answer, answerTime);

            setAnswered(true);
            setAnswering(false);
        },
        [round, onAnswer, tasks],
    );

    const buttonAnswer = useCallback(
        async (answer: number) => {
            await submitAnswer(answer);
        },
        [submitAnswer],
    );

    const keyboardAnswer = useCallback(
        async (answer: number) => {
            if (answeringRef.current || answered) {
                return;
            }
            await submitAnswer(answer);
        },
        [submitAnswer, answered],
    );

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const voiceAnswer = useCallback(
        debounce(async (vAnswer: string) => {
            if (answeringRef.current || answered) {
                return;
            }

            const answer = tasks.findIndex(c => c.voice === vAnswer);
            console.log('Voice answer:', vAnswer, answer);
            if (answer < 0) {
                return;
            }

            await submitAnswer(answer);
        }, 300),
        [submitAnswer, answered],
    );

    useWebSpeechApi(!answering, voiceAnswer);

    const onKeyDown = useCallback(
        async (key: string) => {
            if (answered) {
                // console.log("Key:", key);
                if (key === 'Enter' || key === ' ') {
                    // console.log("Next");
                    onNext();
                }
                return;
            }

            const parsed = Number.parseInt(key);
            if (Number.isNaN(parsed)) {
                return;
            }

            await keyboardAnswer(parsed - 1);
        },
        [answered, onNext, keyboardAnswer],
    );

    const nextRound = useCallback(() => {
        if (answered) {
            onNext();
        }
    }, [answered, onNext]);

    useEventListener('keydown', event =>
        onKeyDown((event as KeyboardEvent).key),
    );

    return (
        <Box
            className={styles.fullScreen}
            style={{backgroundColor: round.task.color}}
            onClick={nextRound}
        >
            {!answered && !answering && (
                <Stack direction="row" flex={1} flexWrap="wrap">
                    {tasks.map((c, i) => (
                        <Box
                            flex={1}
                            minWidth="50%"
                            className={styles.optionButton}
                            position="relative"
                            key={c.color}
                        >
                            <ButtonBase
                                onClick={() => buttonAnswer(i)}
                                sx={{width: '100%', height: '100%'}}
                            />
                            <Box className={styles.optionLabel}>{c.label}</Box>
                        </Box>
                    ))}
                </Stack>
            )}
            <CSSTransition
                in={answering}
                classNames="fade"
                nodeRef={loadingIndicatorRef}
                unmountOnExit
                addEndListener={() => {}}
            >
                <div
                    className={cx(styles.centered, styles.noInteraction)}
                    ref={loadingIndicatorRef}
                >
                    <div
                        className={cx(
                            styles.answeringProgressContainer,
                            styles.withBackground,
                        )}
                    >
                        <CircularProgress
                            color="inherit"
                            sx={{display: 'block'}}
                        />
                    </div>
                </div>
            </CSSTransition>
            {answered && (
                <div className={`${styles.bottom} ${styles.withBackground}`}>
                    Press ENTER or SPACE to continue ...
                </div>
            )}
            <div className={styles.roundCounter}>
                {round.order + 1}/{totalRounds}
            </div>
        </Box>
    );
}
